import { Item } from "./Item";
import { Character } from "../character/Character";

export class HealthPotion implements Item {
  name = "Health Potion";
  cooldownTurns = 3;
  currentCooldown = 0;

  private getRandomHealAmount(): number {
    const rand = Math.random();
    if (rand < 0.5) return 30; // 50%
    else if (rand < 0.9) return 40; // 45%
    else return 70; // 5%
  }

  use(user: Character): string {
    if (this.currentCooldown > 0) {
      return `Ìó¨Ïä§ Ìè¨ÏÖòÏùÄ ÏïÑÏßÅ ${this.currentCooldown}ÌÑ¥ ÎÇ®ÏïÑ ÏÇ¨Ïö©Ìï† Ïàò ÏóÜÏäµÎãàÎã§.`;
    }

    const healAmount = this.getRandomHealAmount();
    const healed = Math.min(user.maxHp - user.hp, healAmount);

    user.hp += healed;
    this.currentCooldown = this.cooldownTurns;

    return `${user.name}Í∞Ä Ìó¨Ïä§ Ìè¨ÏÖòÏúºÎ°ú ${healed}Ïùò HPÎ•º ÌöåÎ≥µÌñàÏäµÎãàÎã§. Îã§Ïùå ÏÇ¨Ïö©ÍπåÏßÄ ${this.cooldownTurns}ÌÑ¥ ÎÇ®ÏïòÏäµÎãàÎã§.`;
  }

  isAvailable(): boolean {
    return this.currentCooldown === 0;
  }

  advanceTurn(): void {
    if (this.currentCooldown > 0) {
      this.currentCooldown--;
    }
  }
}
