import { Skill } from "./Skill";
import { Character } from "../character/Character";

export class Fireball implements Skill {
  name = "Fireball";
  cooldownTurns = 5;
  currentCooldown = 0;

  private getRandomDamage(): number {
    const rand = Math.random();
    if (rand < 0.6) return 20; // 60%
    else if (rand < 0.9) return 25; // 30%
    else return 40; // 10%
  }

  use(user: Character, target: Character): string {
    if (this.currentCooldown > 0) {
      return `ÌååÏù¥Ïñ¥Î≥ºÏùÄ ÏïÑÏßÅ ${this.currentCooldown}ÌÑ¥ ÎÇ®ÏïÑ ÏÇ¨Ïö©Ìï† Ïàò ÏóÜÏäµÎãàÎã§.`;
    }

    let damage = this.getRandomDamage();
    target.takeDamage(damage);
    this.currentCooldown = this.cooldownTurns;
    return `${user.name}Í∞Ä ${target.name}ÏóêÍ≤å ${damage}Ïùò ÌååÏù¥Ïñ¥Î≥º Îç∞ÎØ∏ÏßÄÎ•º ÏûÖÌòîÏäµÎãàÎã§. Îã§Ïùå ÏÇ¨Ïö©ÍπåÏßÄ ${this.currentCooldown}ÌÑ¥ ÎÇ®ÏïòÏäµÎãàÎã§.`;
  }

  isAvailable(): boolean {
    return this.currentCooldown === 0;
  }

  advanceTurn(): void {
    if (this.currentCooldown > 0) {
      this.currentCooldown--;
    }
  }
}
