// ÌÑ¥ Í¥ÄÎ¶¨, ÌîåÎ†àÏù¥Ïñ¥ Ïï°ÏÖò Îã¥Îãπ

import { Player } from '../character/Player';
import { Enemy } from '../character/Enemy';
import { Skill } from '../skill/Skill';
import { Item } from '../item/Item';
import { EnemyAI } from '../ai/EnemyAi';

type turnners = 'player' | 'enemy' | 'nobody'

export class BattleManager {
  private currentTurn: turnners = 'nobody';
  private ai: EnemyAI;
  private turnIntervalId: number | null = null;
  private onTurnChanged?: ((turn:turnners) => void) | null = null ;
  private onCooldownChanged?: (() => void) | null = null ;

  // BattleManager ÏÉùÏÑ± Ïãú EnemyAI ÏÉùÏÑ± ÌõÑ ai Ïóê Ïó∞Í≤∞
  constructor(private player: Player, private enemy: Enemy) {
    this.ai = new EnemyAI(enemy, player);
    this.setCurrentTurn();
  }

  playerAction(
    action: 'attack' | 'heal' | 'skill' | 'item',
    skill?: Skill,
    item?: Item
  ): string {
    if (this.currentTurn !== 'player' || this.isBattleOver()) {
      return 'ÌîåÎ†àÏù¥Ïñ¥ ÌÑ¥Ïù¥ ÏïÑÎãôÎãàÎã§.';
    }

    let msg = '';

    // playerActionÏù¥ Ìò∏Ï∂ú Îê¨ÏùÑ Îïå actionÏùò Í∞íÏóê Îî∞Îùº Ï≤òÎ¶¨ actionÏùÄ Î≤ÑÌäº ÌÅ¥Î¶≠ÏúºÎ°ú ÏßÑÌñâ.
    switch (action) {
      case 'attack':
        msg = this.playerAttack();
        break;
      case 'heal':
        msg = this.playerHeal();
        break;
      case 'skill':
        if (skill) msg = this.playerUseSkill(skill);
        break;
      case 'item':
        if (item) msg = this.playerUseItem(item);
        break;
    }
    //playerAction ÌõÑÏóî ÌÑ¥ÏùÑ Ï§ëÎ¶Ω ÌÑ¥ÏúºÎ°ú Î≥ÄÍ≤Ω -> playerÏùò ÌÑ¥ Ìè¨Ïù∏Ìä∏Î•º Ï¥àÍ∏∞Ìôî
    this.currentTurn = 'nobody';
    this.player.initTurn();
    // this.setCurrentTurn();
    //ÏÇ¨Ïö©Ìïú Ïä§ÌÇ¨ Ïø®Îã§Ïö¥ Ïπ¥Ïö¥Ìä∏ Í∞êÏÜå Ï†ÅÏö©
    if (this.onCooldownChanged) this.onCooldownChanged([]);
    return msg ;
  }

  enemyAction(): string {
    if (this.currentTurn !== 'enemy' || this.isBattleOver()) return '';
    const msg = this.ai.decideAction();
    //playerAction ÌõÑÏóê ÌÑ¥ÏùÑ Ï§ëÎ¶ΩÌÑ¥ÏúºÎ°ú Î≥ÄÍ≤Ω -> enemyÏùò ÌÑ¥ Ìè¨Ïù∏Ìä∏Î•º Ï¥àÍ∏∞Ìôî
    this.currentTurn = 'nobody';
    this.enemy.initTurn();
    this.setCurrentTurn();
    return msg;
  }


  // ÌîåÎ†àÏù¥Ïñ¥Í∞Ä ÏÑ†ÌÉù Í∞ÄÎä•Ìïú ÌñâÎèôÎì§Îì§

  private playerAttack(): string {
    const damage = this.enemy.takeDamage(this.player.attack);
    let msg = `${this.player.name}Í∞Ä ${this.enemy.name}ÏóêÍ≤å ${damage}Ïùò Îç∞ÎØ∏ÏßÄÎ•º ÏûÖÌòîÏäµÎãàÎã§.`;
    if (!this.enemy.isAlive()) msg += ` ${this.enemy.name}Í∞Ä Ïì∞Îü¨Ï°åÏäµÎãàÎã§!`;
    return msg;
  }

  private playerHeal(): string {
    const healed = this.player.heal(20);
    return `${this.player.name}Í∞Ä ${healed}Ïùò HPÎ•º ÌöåÎ≥µÌñàÏäµÎãàÎã§.`;
  }

  private playerUseSkill(skill: Skill): string {
    let msg = skill.use(this.player, this.enemy);
    if (!this.enemy.isAlive()) msg += ` ${this.enemy.name}Í∞Ä Ïì∞Îü¨Ï°åÏäµÎãàÎã§!`;
    return msg;
  }

  private playerUseItem(item: Item): string {
    return item.use(this.player);
  }
  // ÌòÑÏû¨ ÌÑ¥ ÌôïÏù∏
  getCurrentTurn(): turnners {
    console.log("ÌòÑÏû¨ ÌÑ¥ : " + this.currentTurn);
    return this.currentTurn;
  }

  //
  setCurrentTurn() : string{
    // Ï†ÑÌà¨Ï§ëÏù∏ Ï∫êÎ¶≠ÌÑ∞Îì§Ïùò ÌÑ¥ ÌôïÏù∏ @Î¨¥Ï°∞Í±¥ player Ïö∞ÏÑ†
    if (this.currentTurn !== 'nobody') return '';

    if (this.player.isTurn()) {
      this.currentTurn = 'player';
      if (this.onTurnChanged) this.onTurnChanged(this.currentTurn); //callbackÏù¥ ÏûàÏúºÎ©¥ Ïã§Ìñâ
      return 'playerÎãòÏùò ÌÑ¥!';
    }
    if (this.enemy.isTurn()) {
      this.currentTurn = 'enemy';
      if (this.onTurnChanged) this.onTurnChanged(this.currentTurn);
      return 'enemyÎãòÏùò ÌÑ¥!';
    }
    console.log(this.player.getTurnPoint() + " p : e " + this.enemy.getTurnPoint());
    return 'ÌÑ¥ Í≤∞Ï†ïÏ§ë';
  }
  // ÌÑ¥ÏùÑ Í¥ÄÎ¶¨ÌïòÎäî Î£®ÌîÑ Ïã§Ìñâ 
  startTurnLoop():string {
    let msg : string = '';
    if (this.turnIntervalId !==null) return msg;
    this.turnIntervalId = window.setInterval(() => {
      if (this.currentTurn === 'nobody') {
        msg += this.setCurrentTurn();
        this.player.increaseTurnPoint();
        this.enemy.increaseTurnPoint();
      }
    }, 500); // 0.1Ï¥àÎßàÎã§ Ï≤¥ÌÅ¨
    return msg;
  }
  // ÌÑ¥ÏùÑ Í¥ÄÎ¶¨ÌïòÎäî Î£®ÌîÑÎ•º Ï§ëÏßÄ 
  stopTurnLoop(){
    if(this.turnIntervalId !== null){
      clearInterval(this.turnIntervalId);
      this.turnIntervalId = null;
    }
  }

  // ÏΩúÎ∞± Ìï®Ïàò ÏÉùÏÑ±
  setTurnChangedCallback(callback:(turn:turnners)=>void){
    this.onTurnChanged = callback;
  }

  setSkillCooldownCallback(callback:()=>void){
    this.onCooldownChanged = callback;
  }


  isBattleOver(): boolean {
    return !this.player.isAlive() || !this.enemy.isAlive();
  }

}
