import { Character } from './Character';

export class Player extends Character {
  heal(amount: number): number {
    const healed = Math.min(this.maxHp - this.hp, amount);
    this.hp += healed;
    return healed;
  }

  // Ï≤¥Î†• 30% Ïù¥ÌïòÏùº Îïå ÏûêÎèô ÌöåÎ≥µÌïòÎäî Ìå®ÏãúÎ∏å
  usePassiveSkill(): string {
    if (this.hp / this.maxHp < 0.3) {
      const healed = this.heal(10);
      return `${this.name}Ïùò Ìå®ÏãúÎ∏å Ïä§ÌÇ¨ Î∞úÎèô! HP ${healed} ÌöåÎ≥µ`;
    }
    return '';
  }
}
